
import XHeader from 'vux/src/components/x-header/index.vue'
import Actionsheet from 'vux/src/components/actionsheet/index.vue'
import TransferDom from 'vux/src/directives/transfer-dom/index.js'
import ButtonTab from 'vux/src/components/button-tab/button-tab.vue'
import ButtonTabItem from 'vux/src/components/button-tab/button-tab-item.vue'

import { ajax2 } from '../common/js/ajax'
import { utils } from '../common/js/utils'
import share from '../components/share.vue'
import {mapState,mapMutations,mapGetters} from 'vuex'


export default{

components:{
XHeader,
Actionsheet,
TransferDom,
ButtonTab,
ButtonTabItem,
share
},
data(){
return {
newsItem:{},
newsInfo:{},
souceUrl:this.$route.params.id,
newitem:{},
newShow:1,             //新闻详情展示
isShare:false,        //分享组件
isCollection:false
}
},
created(){

},
mounted(){
this.requestInfo();
},
methods:{
requestInfo(){
if(this.souceUrl){
var url = 'https://m.toutiao.com/' + this.souceUrl + '/info/';
let self = this;
ajax2(url,function(data){
self.newsInfo = data.data;
self.newShow = 2;
},function(msg){
alert(msg);
})
}else{
console.log('离开本页，不执行请求！');
}
},


unfold_field(){
this.$refs.news_content.style.height = 'auto';
this.$refs.unfold_field.style.display = 'none';
}
,share(){
this.isShare = true;
document.getElementsByTagName('body')[0].style.overflow = 'hidden';
}
,hide_Share(){
this.isShare = false;
}
,collection(){
console.log('收藏');
if(!this.isCollection){
console.log(this.newitem.item_id);
this.$store.commit('addCollection',this.newitem);
this.isCollection = !this.isCollection;
}else{

}
}
},
watch:{
'$route'(){



this.$refs.news_content.style.height = '800px';
this.$refs.unfold_field.style.display = 'block';

}
},
filters:{
formatTime(time){
utils.dateFormat();
let time1 = time + '000';
let time2 = new Date(parseInt(time1));
let time3 = time2.format('MM-dd hh:mm:ss');
return time3;
}
},
activated(){
this.souceUrl = this.$route.params.id;
this.newitem = JSON.parse(this.$route.query.newsItem);
this.requestInfo();
this.isCollection = false;
},
updated(){












}
}



