export default{
    props:['isShow'],
    data(){
        return {
            shareItem:{
                '1':'weibo',
                '2':'github',
                '3':'weibo'
            }
        }
    },
    methods:{
        cancelShare(){
            document.getElementsByTagName('body')[0].style.overflow = 'auto';
            this.$emit('hideShare');
        },
        share(key){
            alert('分享' + this.shareItem[key]);
        }
    }
}



